import type { MessageEntity } from '../Client/Managers/MessagesManager'
import { Builder } from './Builder'

export interface PollOptionPayload {
    text: string
    text_parse_mode?: string
    text_entities?: MessageEntity[]
}

export class PollOption extends Builder implements PollOptionPayload {

    public declare text: string
    public constructor(text: string, text_parse_mode?: string, text_entities?: MessageEntity[] ){
        super({ text, text_parse_mode, text_entities })
    }

}

export class PollOptions extends Builder {

    public declare value: PollOptionPayload[]
    public constructor(...options: PollOption[]){
        super({ value: JSON.stringify(options.map((value: PollOption) => value.parse())) })
    }

}